import React from 'react';

const Person = ({ person }) => (
  <>
    {person.sex === "f" ? (
      <tr className="person person--female">
        <td>{person.id}</td>

        {person.born < 1650 ? (
          <td className="born-before-1650">{person.name}</td>
        ) : (
          <>
            {person.died > 1800 ? (
              <td className="died-after-1800">{person.name}</td>
            ) : (
              <td>{person.name}</td>
            )}
          </>
        )}
        <td>{person.sex}</td>
        <td>{person.born}</td>
        <td>{person.died}</td>
        <td>{person.died - person.born}</td>
        <td>{Math.ceil(person.died / 100)}</td>
        <td>{person.father}</td>
        <td>{person.mother}</td>
      </tr>
    ) : (
      <tr className="person person--male">
        <td>{person.id}</td>
        {person.born < 1650 ? (
          <td className="born-before-1650">{person.name}</td>
        ) : (
          <>
            {person.died > 1800 ? (
              <td className="died-after-1800">{person.name}</td>
            ) : (
              <td>{person.name}</td>
            )}
          </>
        )}
        <td>{person.sex}</td>
        <td>{person.born}</td>
        <td>{person.died}</td>
        <td>{person.died - person.born}</td>
        <td>{Math.ceil(person.died / 100)}</td>
        <td>{person.father}</td>
        <td>{person.mother}</td>
      </tr>
    )}
  </>
);

export default Person;
